$(function() {
    let $header = $('.header');
    let $doc = $(document);
    let $win = $(window);
    let windowWidth;
    let timeOut = 100;

    let $btnPriceLeft = $('.price__leftBtn');
    let $btnPriceRight = $('.price__rightBtn');
    let $priceItems = $('.price .priceItem');
    let itemPrice = 0;

    let $btnTabs = $('.devItems__title');
    let $list = $('.devItems__list');


    $btnPriceLeft.on('click', prevPrice);
    $btnPriceRight.on('click', nextPrice);
    $btnTabs.on('click', runTabs);


    getViewportWidth();
    autoRunFirstTab();


    $doc.on('scroll', function () {
        delayer(function () {
            docScrolling();
            headerScroll();
        },timeOut);
    });

    $win.on('resize', function () {
        delayer(function () {
            setViewport();
            headerRunnerOff();
        },timeOut);
    });


    let delayer = delay();

    function delay() {
        let timer;
        return function (f, t) {
            clearTimeout(timer);
            timer = setTimeout(f, t);
        }
    }

    function docScrolling() {
       let scrollDoc = $('html, body').scrollTop();
       return scrollDoc;
    }

    function getViewportWidth() {
        let viewportWidth;

        if (typeof window.innerWidth != 'undefined') {
            viewportWidth = window.innerWidth;
        }

        else if (typeof document.documentElement != 'undefined'
           && typeof document.documentElement.clientWidth !=
           'undefined' && document.documentElement.clientWidth != 0) {

            viewportWidth = document.documentElement.clientWidth;
       }

        else {
            viewportWidth = document.getElementsByTagName('body')[0].clientWidth;
       }

       return {
            width: viewportWidth,
        }
    }

    let setViewport = function () {
        windowWidth = getViewportWidth().width;
    };

    function headerScroll() {
        setViewport();

        if(
            windowWidth < 756 ||
            windowWidth >= 1170
        ){
            headerRunnerOn();
         }

    }

    function headerRunnerOn() {
        if(
            docScrolling() > 200
        ){
            $header.slideDown(200).addClass('active');
        }
        else if (
            docScrolling() <= 200
        ) {
            $header.slideUp().removeClass('active')
        }
    }

    /*проверка на наличие класса 'active'*/
    headerRunnerOff();
    function headerRunnerOff() {
        setViewport();

        if(
            windowWidth >= 756 &&
            windowWidth < 1170
        ){
            $header.slideDown(10).addClass('active');
        }
        else {
            $header.slideUp(10).removeClass('active');
        }
    }

    /*--------------------------------*/

    /*Слайдер тарифов Битрикс24*/
    $priceItems.eq(1).addClass('showThis');

    function nextPrice() {
        $priceItems.eq(itemPrice).removeClass('showThis');

        itemPrice++;

        if(itemPrice >= $priceItems.length){
            itemPrice = 0;
        }

        $priceItems.eq(itemPrice).addClass('showThis');
    }

    function prevPrice() {
        $priceItems.eq(itemPrice).removeClass('showThis');

        itemPrice--;

        if(itemPrice < 0){
            itemPrice = $priceItems.length - 1;
        }

        $priceItems.eq(itemPrice).addClass('showThis');
    }

    /*--------------------------------*/

    /*Аккордион и табы*/
    function autoRunFirstTab() {
        $list.eq(0).show();
        $btnTabs.eq(0).addClass('active');
    }

    function runTabs() {
        setViewport();

        if(windowWidth < 756){
            $(this).addClass('active').siblings().removeClass('active');
            let $tab = $(this).next($list).stop(true).slideDown(400);
            $list.not($tab).stop(true).slideUp(400);
        }
        else if(windowWidth >= 756){
            console.log(1);
        }
    }


}); // load